import React from 'react';
import ReactDOM from 'react-dom';
import {Link} from 'react-router-dom';

const NotFoundPage = () => {
    return (
        <div>
            <p>404! - <Link to="/">Go Home</Link> </p>
        </div>
    );
};

export default NotFoundPage;


